export default {
	data() {
		return {
			chongdian:'开启充电',
			chongdianzhong:'充电中',
			yue: '￥100',
			show: true,
			showe: false,
		};
	},
	methods: {
		kaiqi() {
			// console.log('开启充电');
			// uni.navigateTo({
			// 	url: 'chongdian'
			// });
			this.show = false;
			this.showe = true;
			uni.showActionSheet({
				itemList: ['开启充电', '温馨提示', '充电失败'],
				success: function (res) {
					if(res.tapIndex + 1 == 1 ){
						
					}else if(res.tapIndex + 1 == 2 ){
						uni.showModal({
						    title: '温馨提示',
						    content: '充电桩温度过高，请稍后再充电！',
							showCancel:false,
						    success: function (res) {
						        if (res.confirm) {
						           uni.switchTab({
						           	url: '../index/index'
						           });
						        }
						    }
						});
					}else if(res.tapIndex + 1 == 3 ){
						uni.navigateTo({
							url: 'shibai'
						});
					}
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			    // success: function (res) {
			    //     if (res.confirm) {
			    //         uni.navigateTo({
			    //         	url: 'jieshu'
			    //         });
			    //     } else if (res.cancel) {
			    //         uni.navigateTo({
			    //         	url: 'shibai'
			    //         });
			    //     }
			    // }
			});
		},
		jieshu() {
			console.log('结束充电');
			// uni.showModal({
			// 	content: '扫码事件',
			// 	showCancel: false
			// });
			// uni.scanCode({//扫一扫
			// 	onlyFromCamera: true,
			// 	success: function(res) {
			// 		console.log(JSON.stringify(res));
			// 	}
			// });
			uni.switchTab({
				url: '../index/index'
			});
		},
		cdz() {
			uni.showModal({
			    title: '充电中',
			    content: '充电中，是否要结束！',
				showCancel:true,
			    success: function (res) {
			        if (res.confirm) {
			           // uni.switchTab({
			           // 	url: '../index/index'
			           // });
					   uni.showModal({
					   	content: '继续充电',
					   	showCancel: false
					   });
			        }else{
						uni.showModal({
							content: '结束充电',
							showCancel: false
						});
					}
			    }
			});
		}
	}
};
